<template>
	<view class="">
		<view class="iconFix">
			<!-- <view class="donghua"></view> -->
			<!-- <view style="text-align: center;margin-bottom: 10px;" v-show="!dhShow">轻触说话</view> -->
			 <!-- @tap="startRecord" -->
			<view class="iconView" @touchstart="startRecord" @touchend="endRecord">
				<uni-icons type="mic-filled" size="30" color="#fff"></uni-icons>
			</view>
		</view>
		<view class="waveWrapper waveAnimation" v-show="dhShow">
			<view class="zi">{{text}}</view>
			<view class="waveWrapperInner bgTop">
				<view class="wave waveTop"></view>
			</view>
			<view class="waveWrapperInner bgMiddle">
				<view class="wave waveMiddle"></view>
			</view>
			<view class="waveWrapperInner bgBottom">
				<view class="wave waveBottom"></view>
			</view>
		</view>
		
		<u-popup :show="popupShow" mode="center" class="u-popup-center" :closeable="true" @close="popupShow=false">
			<text class="popup-title">为您匹配以下内容，请选择</text>
			<view class="searchRes">
				<view class="searchResList" v-for="(item,index) in serachCon" @tap="jumpFunc(item)" v-html="item.name">{{item.name}}</view>
			</view>
		</u-popup>
		<u-toast ref="uToast"></u-toast>
		
	</view>
</template>

<script>
	export default {
		data() {
			return {
				text: "",
				dhShow: false,
				jumpArr: getApp().globalData.appArr,
				popupShow: false,
				myAuth: uni.getStorageSync("myAuth"),
				serachCon:[],
				access_token:""
			}
		},
		onReady() {
			
		},
		methods: {
			startRecord() {
				let _this = this
				this.dhShow = true
				plus.device.vibrate(300)
				var options = {};
				options.continue = true ;// true表示语音引擎不会根据语音输入自动结束
				options.engine = 'baidu';
				options.userInterface = false ; // true显示默认用户界面
				options.lang = 'zh-cn';// "zh-cn"-中文，普通话；"en-us"-英语；"zh-cantonese"-中文，粤语；"zh-henanese"-中文,河南话（百度语音识别不支持河南话）
				options.punctuation = false;// 识别结果中是否包含标点符号
				
				plus.speech.startRecognize(options, function(s) {
					_this.text += s;
					console.log("_this.text",_this.text)
					// _this.searchFunc(_this.text)
					
				}, function(e) {
					_this.$refs.uToast.show({
						type:"error",
						message: "语音识别失败"
					})
					_this.dhShow = false
				});
				// setTimeout(this.endRecord, 10000);
			},
			endRecord() {
				this.dhShow = false
				plus.speech.stopRecognize();
			},
			searchFunc(words){
				let searchArr=[]
				for(var i in this.jumpArr){
					if(words.indexOf(this.jumpArr[i].name)>-1){
						searchArr.push(this.jumpArr[i])
					}
					// 短文本相似度匹配
					uni.request({
						url: `https://aip.baidubce.com/rpc/2.0/nlp/v2/simnet?access_token=${uni.getStorageSync("nlpToken")}`,
						data: {
							text_1: words,
							text_2: this.jumpArr[i].name
						},
						method: 'POST',
						header: {
							'Content-Type': 'application/json'
						},
						success: (res) => {
							// console.log("短文本相似度结果", res)
							if(res.data.score>=0.8){
								searchArr.push(this.jumpArr[i])
							}
						}
					})
					
				}
				
				// .........................................................................
				if(searchArr.length==0){
					this.$refs.uToast.show({
						type:"error",
						message: "没有找到对应内容！"
					})
				}else if(searchArr.length==1){
					this.jumpFunc(searchArr[0])
				}else{
					this.popupShow=true
					this.serachCon=searchArr
				}
				
			},
			jumpFunc(obj){
				this.popupShow=false
				// else if(obj.name=="快报"){
				// 	if(obj.permissionName){
				// 		if(this.myAuth.indexOf(obj.permissionName)>-1){
				// 			this.$emit("voicePress",obj.name);
				// 		}else{
				// 			this.$refs.uToast.show({
				// 				type:"error",
				// 				message: "抱歉，您没有该功能权限！"
				// 			})
				// 		}
				// 	}
				// }
				if(obj.name=="商城"){
					plus.runtime.openURL(obj.url)
				}else{
					let rout=obj.url
					if(rout[rout.length-1]=="reports"){
						
						if(obj.permissionName){
							if(this.myAuth.indexOf(obj.permissionName)>-1){
								uni.navigateTo({
									url: `${obj.url}?title=${obj.name}`
								})
							}else{
								this.$refs.uToast.show({
									type:"error",
									message: "抱歉，您没有该功能权限！"
								})
							}
						}
						
					}else{
						
						if(obj.permissionName){
							if(this.myAuth.indexOf(obj.permissionName)>-1){
								uni.navigateTo({
									url: obj.url
								})
							}else{
								this.$refs.uToast.show({
									type:"error",
									message: "抱歉，您没有该功能权限！"
								})
							}
						}
						
					}
				}
			},
			
			
			
		}
	}
</script>

<style lang="scss" scoped>
	.box .u-popup-center .u-popup__content > uni-view > *{
		margin-top: 0;
	}
	.searchRes{
		margin-top: 8px;
		.searchResList{
			padding: 8px 10px;
			border-bottom: 1px solid #EAEBEC;
		}
		.searchResList:nth-child(even){
			background-color: #FAFAFA;
		}
	}
	
	.iconFix {
		position: fixed;
		bottom: 24px;
		left: calc(50% - 25px);
		z-index: 9980;
	}

	.iconFix .iconView {
		width: 30px;
		padding: 10px;
		border-radius: 50%;
		background-image: linear-gradient(to top, #087AB8, #43BAFC);
	}

	.iconFix>* {
		margin: auto;
	}

	//..........................................................................................
	@keyframes move_wave {
		0% {
			transform: translateX(0) translateZ(0) scaleY(1)
		}

		50% {
			transform: translateX(-25%) translateZ(0) scaleY(0.55)
		}

		100% {
			transform: translateX(-50%) translateZ(0) scaleY(1)
		}
	}

	.waveWrapper {
		width: 100%;
		height: 400px;
		overflow: hidden;
		position: absolute;
		left: 0;
		bottom: 0px;
		margin: auto;
		transition-duration: 0.5s;
		box-shadow: 0px -1px 14px #dfdfdf;
		background-image: linear-gradient(to top, rgba(255,255,255,1), rgba(255,255,255,.6));
	}
	
	.zi{
		box-sizing: border-box;
		font-size: 20px;
		text-align: center;
		position: absolute;
		width: 100%;
		padding: 60px 20px 0;
	}
	
	.waveWrapperInner {
		position: absolute;
		width: 100%;
		height: 160px;
		overflow: hidden;
		bottom: -1px;
		background-image: linear-gradient(to top, #e4d0ff 20%, #fff 80%);
	}

	.bgTop {
		z-index: 15;
		opacity: 0.5;
	}

	.bgMiddle {
		z-index: 10;
		opacity: 0.75;
	}

	.bgBottom {
		z-index: 5;
	}

	.wave {
		position: absolute;
		left: 0;
		width: 200%;
		height: 100%;
		background-repeat: repeat no-repeat;
		background-position: 0 bottom;
		transform-origin: center bottom;
	}

	.waveTop {
		background-image: url(@/static/images/wave-top.png);
		background-size: 50% 100px;
	}

	.waveAnimation .waveTop {
		// animation: move-wave 3s;
		// -webkit-animation: move-wave 3s;
		// -webkit-animation-delay: 1s;
		// animation-delay: 1s;
		animation: move_wave 7s linear infinite;
	}

	.waveMiddle {
		background-image: url(@/static/images/wave-mid.png);
		background-size: 50% 120px;
	}

	.waveAnimation .waveMiddle {
		animation: move_wave 10s linear infinite;
	}

	.waveBottom {
		background-image: url(@/static/images/wave-bot.png);
		background-size: 50% 100px;
	}

	.waveAnimation .waveBottom {
		animation: move_wave 15s linear infinite;
	}
</style>
